import { AvatarName } from 'components/avatars';
import { Translate } from 'components/language';
import { Instruction } from 'components/text';
import { CardHighlight, PointsHighlight } from './Highlights';

type WordSelectionRulesProps = {
  scout: GamePlayer;
};

export function WordSelectionRules({ scout }: WordSelectionRulesProps) {
  return (
    <Instruction>
      <Translate
        pt={
          <>
            Cada rodada tem seu tema... uma palavra.
            <br />
            Para essa rodada, <AvatarName player={scout} addressUser size="small" /> escolher o tema.
          </>
        }
        en={
          <>
            Every round has a theme... a word.
            <br />
            For this round,
            <AvatarName player={scout} addressUser size="small" /> will choose the theme.
          </>
        }
      />
    </Instruction>
  );
}

export function GeneralRules() {
  return (
    <Instruction contained>
      <Translate
        pt={
          <>
            Somos caadores de sonhos!
            <br />
            Dentro do tema escolhido, podemos visitar os sonhos de at 10 das 15 pessoas (cartas-imagens)
            disponveis.
            <br />
            O objetivo  encontrar uns aos outros em cada sonho, ento escolha apenas sonhos que voc acredita
            que outros jogadores tambm iro escolher.
            <br />
            Parece fcil, mas se voc escolher um sonho que ningum mais escolheu... vixi...
          </>
        }
        en={
          <>
            We are dream scouts!
            <br />
            Given the chosen theme, we can visit dreams of 10 of the 15 people (image cards) available.
            <br />
            The goal of the game is to match the same visited dreams of other players, therefore choose dreams
            that you think other players will also choose.
            <br />
            It seems easy, but if you choose a dream that nobody else has chosen... well...
          </>
        }
      />
    </Instruction>
  );
}

export function DreamSelectionRules({ contained = false }) {
  return (
    <Instruction contained={contained}>
      <Translate
        pt={
          <>
            Selecione sonhos (cartas) que voc acha que se relacionam com a palavra-tema da rodada.
            <br />
            Voc deve selecionar pelo menos <CardHighlight>1</CardHighlight> e no mximo{' '}
            <CardHighlight>10</CardHighlight>.
          </>
        }
        en={
          <>
            Now select cards that you think match the round's card.
            <br />
            You must select at least <CardHighlight>1</CardHighlight> card and a maximum of{' '}
            <CardHighlight>10</CardHighlight>.
          </>
        }
      />
    </Instruction>
  );
}

export function DreamSelectionExtendedRules() {
  return (
    <Instruction contained>
      <Translate
        pt={
          <>
            Na prxima fase, de um a um, cada jogador vai escolher um de seus sonhos visitados.
            <br />
            Se pelo menos um jogador visitou o mesmo sonho, eles ganham pontos. Se ningum escolheu, o jogador
            est fora da rodada!
            <br />
            Dai passa-se a vez para o prximo jogador!
            <br />
            Sonhos s podem ser escolhidos uma vez por rodada...
          </>
        }
        en={
          <>
            On the next phase, one by one, each player will select one of their visited dreams.
            <br />
            If at least one other player visited the same dream, they get points! If nobody visited the same
            dream, you are out of the round!
            <br />
            Each dream can be chosen once per round only...
          </>
        }
      />
    </Instruction>
  );
}

export function CardPlayRules() {
  return (
    <Instruction contained>
      <Translate
        pt={
          <>
            De um em um, cada jogador vai escolher um de seus sonhos visitados se voc ainda tem algum
            disponvel.
            <br />
            Selecione o sonho que voc mais acha que tem chance de outro jogador ter tambm visitado.
            <br />
            Voc ganha <PointsHighlight type="positive">3</PointsHighlight> pontos se apenas mais 1 jogador
            visitou o mesmo sonho.
            <br />
            Voc ganha <PointsHighlight type="positive">2</PointsHighlight> pontos se mais de 1 jogador
            visitou o mesmo sonho.
            <br />
            Voc est eliminado da rodada se ningum mais visitou o sonho escolhido.
          </>
        }
        en={
          <>
            One by one, each player chooses one of their visited dreams, if you have any still available.
            <br />
            Select the dream you think you have the most chances of matching another player.
            <br />
            You get <PointsHighlight type="positive">3</PointsHighlight> points if only 1 player visited the
            same dream.
            <br />
            You get <PointsHighlight type="positive">2</PointsHighlight> points if many players have visited
            the same dream.
            <br />
            You are eliminated for this round if nobody else has visited the chosen dream.
          </>
        }
      />
    </Instruction>
  );
}

export function BotsRules() {
  return (
    <Instruction contained>
      <Translate
        pt={
          <>
            Em um jogo com bots, 2 jogadores-robs so adicionados ao jogo.
            <br />
            Antes da fase do Bingo dos Sonhos, os dois bots entram em ao e selecionam cartas segundo se
            seguinte lgica:
            <br />
            O bot A seleciona todas as cartas que foram mais selecionadas pelos jogadores.
            <br />
            O bot B seleciona cartas que foram selecionadas por apenas um jogador, mas apenas uma por jogador,
            e somente se mais de um jogador tiver essa carta extra.
            <br />O bot C simplesmente seleciona 4 cartas aleatrias.
          </>
        }
        en={
          <>
            In a game with bos, the 2 bots are added to the game.
            <br />
            Before the Dream Bingo phase, the two bots select cards based on a pre-determined logic:
            <br />
            Bot A select all most visited cards.
            <br />
            Bot B select all cards that got a single player visiting them but only one per player and only if
            more than one player will have a match this way.
            <br />
            Bot C simply selects 4 random cards.
          </>
        }
      />
    </Instruction>
  );
}
